import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Status from '../../../../components/Status';
import { useAppSelector } from '../../../../hooks/store';
import MiniBox from '../../../../layout/MiniBox';
import globalStyle from '../../../../Global.module.css';
import { getInTouch } from '../../../../utils/general';

/**
 * Kyb Declined - Component for showing bussiness declined status.
 * @module KYB
 * @returns {JSX.Element}
 */
const KybDeclined = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const accounts = useAppSelector((store) => store.account.accounts, shallowEqual);

  const onNext = useCallback(() => {
    getInTouch();
  }, []);

  return (
    <div className="page">
      <MiniBox
        varient="box"
        heading=" "
        onBack={() => {
          if (accounts.length > 0) {
            navigate('/ao/account/setup-options');
          } else {
            navigate('/ao/account/account-type');
          }
        }}
        bodyClass={globalStyle.sucessScreenNoVPadding}
      >
        <Status
          status="failure"
          title={t('business_declined.title')}
          description={t('business_declined.description')}
          ctaText={t('business_declined.ctaText')}
          onNext={onNext}
        />
      </MiniBox>
    </div>
  );
};

export default KybDeclined;
